.card {
	position: relative;
	display: flex;
	flex-direction: column;
	align-items: center;
	padding: 20px;
	border-radius: 15px;
	width: 200px;
	height: 487px;
	border: 1px solid rgb(231, 231, 231);
	margin-bottom: 2rem;
	margin-top: 1rem;
}
.card:hover {
	box-shadow: 0 2px 4px rgba(40, 41, 61, 0.04), 0 8px 16px rgba(96, 97, 112, 0.16);
}
.card:focus-within {
	box-shadow: 0 0 1px rgba(40, 41, 61, 0.16), 0 0.5px 2px rgba(96, 97, 112, 0.24);
}
.card__desc {
	font-size: 20px;
	padding-top: 18px;
	font-weight: 600;
	position: relative;
}

.card__image {
	width: 100%;
	object-fit:contain;
	margin-bottom: 16px;
	height: 230px;
	border-radius: 10px;
}


.card__old-price {
	font-size: 12px;
	position: absolute;
	top: 0;
	left: 0;
	font-weight: 600;
	line-height: 14px;
	text-decoration: line-through;
	display: block;
	height: 14px;
}
.card_price_wrapper {
	display: flex;
	flex-direction: row;
	justify-content: center;
	gap:15px;
	align-items: center;
	padding-right: 20px;
}
.card__price {
	font-size: 16px;
	font-weight: 600;
	display: block;
	margin-bottom: 6px;
	text-align:center;
}
.card__price_title{
  font-size: 24px;  
}
.card_old_price{
	font-size: 16px;
	line-height: 24px;
	font-weight: 400;
	display: block;
	text-align:right;
	text-decoration: line-through;	
	color: red;
}
.card__price_type_discount {
	color: #F44336;
}
.card__weight {
	font-size: 12px;
	line-height: 14px;
	color: #7b8e98;
	display: block;
	margin-bottom: 2px;
	text-align:right;
}
.card__name {
	font-size: 18px;
	line-height: 1.25;
	font-family: 'Raleway', sans-serif;
	text-align: center;
}
.card__discount {
	padding: 2px 8px;
	font-weight: 800;
	font-size: 16px;
	line-height: 20px;
	color: #FFFFFF;
	background-color: #F44336;
	border-radius: 20px;
}
.card__favorite {
	cursor: pointer;
	background: none;
	border: none;
	padding: 0;
	fill: rgba(255, 216, 216, 0.9);
	transition: ease-in 0.2s;
}
.card__favorite:hover {
	fill: red;
}
.card__favorite_active {
  fill: red;}

.card__cart {
	margin-top: auto;
}
.card__link {
	text-decoration: none;
	color: currentColor;
}
.btn {
	padding: 10px 18px;
	font-size: 16px;
	font-weight: 700;
	line-height: 20px;
	border-radius: 55px;
	text-decoration: none;
	display: inline-block;
	transition: background-color ease-in 0.1s;
	position: absolute;
	bottom:10px;
	margin-top: 10px;
	left:75px
}
.card__cart {
	padding: 14px 30px;
	background-color: #fce388;
	cursor: pointer;
	border-radius: 14px;
	font-size: 16px;
	border: none;
	position: absolute;
	bottom: 6px;
	transition: ease-in 0.2s;
	height: min-content;
	margin-bottom: 15px;
}
.card__cart:hover {
	background-color: #f4bf01;
	box-shadow: #7b8e98 1px 1px;	
}
.card__cart_Disabled {
	background-color: #e1e1e1;	
}
.card__cart_Disabled:hover {
	background-color: #e1e1e1;	
}
.card__cart_InCart{
  background-color: #e40000;	 
}
.card_top{
display: flex;
justify-content: space-between;	
width:210px
}

@media (max-width: 400px) {
  .card {
    position: relative;
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 20px;
    border-radius: 15px;
    width: 120px;
    height: 320px;
    border: 1px solid rgb(231, 231, 231);
    margin-bottom: 2rem;
    margin-top: 1rem; 
  }
  .card__image {
    width: 100%;
    object-fit:contain;
    margin-bottom: 8px;
    height: 130px;
    border-radius: 10px;
  }
  .card__desc {
    font-size: 8px;
    padding-top: 18px;
    font-weight: 600;
    position: relative;
  } 


  .card__price {
    font-size: 12px;
    line-height: 12px;
    font-weight: 600;
    margin-bottom: 3px;
    display: flex;
    justify-content: center;
    align-items: center;
  }
  .card__price_title{
    font-size: 18px;
    display: flex;
    justify-content: center;
    align-items: center; 
  }
  .card__name {
    font-size: 13px;
    font-family: 'Raleway', sans-serif;
    text-align: center;
  }

  .card__cart {
    padding: 12px 20px;
    
    background-color: #fce388;
    cursor: pointer;
    border-radius: 14px;
    font-size: 13px;
    border: none;
    position: absolute;
    bottom: 6px;
    transition: ease-in 0.2s;
    height: min-content;
    margin-bottom: 15px;
  
  }
  .card_old_price{
    font-size: 12px;
    font-weight: 400;
    text-decoration: line-through;	
    color: red;
  
  }
  .card_price_wrapper {
    display: flex;
    justify-content: center;
    gap:10px;
    align-items: center;
    padding-right: 10px;
  }


  .card_top{
    display: flex;
    justify-content: space-between;	
    width:130px
    }
  .card__discount {
    padding: 2px 8px;
    font-weight: 800;
    font-size: 13px;
    line-height: 15px;
    color: #FFFFFF;
    background-color: #F44336;
    border-radius: 20px;
    }
  }