.private_container {
    text-align: right;
    overflow: hidden;
    height: 520px;
    padding-left: 50px;
    padding-right: 50px;
}
.private_user_info_title{
    text-align: left;
    width:fit-content;
    margin-bottom: 15px;
}
.private_user_data_wrapper{
    text-align: left;
    width: 100%;
}
.private_user_data{
    font-size: 28px;
}
.private_user_info_input{
    outline: none !important;;
    border: 1px solid rgb(216, 216, 216);
    border-radius: 5px;
    font-size: 16px;
    padding: 5px 6px 6px 5px;
    min-width: 300px;
    height:18px
}
.private_user_info_input:focus{
    border: 1px solid black;
}
.private_user_info_item_wrapper{
    height:20px;
    display: flex;
    justify-content: space-around;
    align-items:center;
    gap:10px
}
.private_user_info{
display: flex;
justify-content: flex-start;
align-items: flex-start; 
flex-direction: column;
gap: 3rem; 
font-size: 20px;
width: 700px
}
.private_image_info {
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 1rem;
    height: 380px;
    border: 1px rgb(234, 234, 234) solid;
    border-radius: 5px;
    padding-top: 30px;
}
.private_image_wrapper{
    height:230px;
    width:230px 
}
.private_image {
    height:200px;
    width:200px;
    object-fit:cover;
    border-radius: 15px; 
    cursor: pointer;
    border-radius: 50%;
}
.info_grid{
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap:65px;

}
.private_user_info_titles_container{
    display: grid;
    grid-template-rows: repeat(3, 0.4fr);
    width:120%;
}
.private_user_info_inputs_container{
    display: grid;
    grid-template-rows: repeat(3, 0.31fr);
    font-weight: 600;
}
.private_input_icon{
    display: flex;
    align-items: center;
    gap:6px;

}
.private_btn_change{
    padding: 14px 30px;
	background-color: #fce388;
	cursor: pointer;
	border-radius: 10px;
	font-size: 16px;
	border: none;
	width:190px;
	height: 40px;
	display: flex;
	justify-content: center;
	align-items: center;
    margin-bottom: 10px;
    margin-top: 10px;  
}
.private_btn_change:hover{
	background-color: #ffd642;
}
.private_close_icon_wrapper{
    text-align: right;
    
    display: flex;
    justify-content: flex-end;
}
.edit_form_wrapper{
width: 96%;
}
.edit_form_wrapper_grid {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
}

.private_close_form_button {
    background-color: transparent;
    cursor: pointer;
    display: flex;
    justify-content: center;
    align-items: center;
    height:22px;
    width: 22px;
    border-radius: 50%;
    margin-bottom: 15px;
    background-color: #ffea9f;
}
.private_close_form_button:hover{
    background-color: #fecb0f 
}
.private_avatar_modal{
height: 550px;
width: 330px;
text-align: center;   
}
.private_avatar_modal_image{
height: 100%;
width: 100%;
border-radius: 15px;

}
.private_buttons{
    width:100%;
    display: grid;
    grid-template-columns: repeat(2, 0.465fr)
}