body {
  margin: 0;
  max-width:100vw;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  background-color: rgb(255, 248, 227);
  overflow-x:hidden;
  background-image: url("/public/assets/pictures/pyramid-egypt_bg.jpg");
  background-attachment: fixed;
  background-repeat: no-repeat;
  background-size: cover;
}
.container {
  background-color: white;
  max-width: 1124px;
	padding-left: 1rem;
	padding-right: 1rem;
	width: 95%;
	margin: 0 auto;
  font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
  font-size: 16px;
  overflow-x:hidden;
  opacity: 0.95;
  position: relative;
  top:140px;
  min-height:65vh
}
.content {
	padding-right: 30px;
  padding-top: 50px;
  padding-left: 70px;
  padding-bottom: 30px;
}
@media (max-width: 400px) {
  .container {
    width:100%;
    background-color: white;
    padding-left: 1rem;
    padding-right: 1rem;
    margin: 0 auto;
    font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
    font-size: 16px;
    overflow-x:hidden;
    opacity: 0.95;
    position: relative;
    top:260px;
    min-height:65vh
  }
}